body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.modalBtn{
  top:0;
  z-index:99999999999;
  position: absolute;
}



/* react modal custom style */
.ReactModal__Content{
  background:#262626 !important;
  border:none !important;
}

@media(min-width:1200px){
  .ReactModal__Content{
  min-width:600px;
  }
}

.ReactModal__Content .input-group-text{
  border-left:2px solid #525252;
  color:#525252;
}

.ReactModal__Content .modal-close{
top: 5px;
right: 15px;
font-size: 20px;
cursor:pointer;
}

.ReactModal__Overlay{
background:rgba(83, 83, 83, 0.86) !important;
}


.sharedStyles__Base-sc-1k4k4c0-0 .title{
  color:rgba(48, 47, 47, 0.86) !important;
}

