body{
   margin: 0;
   padding: 0;
   overflow: hidden;
   font-family: SegoeUI;
   overflow-y: auto;
   background-color:$body-color;
   font-size: 1rem;
   color: $white;
}

h1 {
   font-size: 1.7rem;
 }
 
 h2 {
   font-size: 1.3rem;
 }
 
 h3 {
   font-size: 1.1rem;
 }
 
 h4 {
   font-size:	1.2rem;
 }
 
 h5 {
   font-size: 0.9rem;
 }
 
 h6 {
   font-size: 0.67em;
 }
 h1,h2,h3,h4,h5,h6{
   font-family: SegoeUI;
   line-height: 1.36;
   color: $white;
   margin-bottom: 0;
 }
 p{
   font-family: SegoeUI;
   color:$white; 
   font-size:15px
 }
ol,ul{
   margin: 0;
   padding: 0;
   list-style-type: none;
}
span{
   color:$white;
}
a,a:hover{
   color:$white;
   text-decoration: none;
}
.heading {
	margin-bottom: 19px;
}
.table {
   margin-bottom:0;
}
.table td{
   font-size:15px;
}
.mb-39{
   margin-bottom:39px;
}
.btn {
	padding: 10px 32px;
   border-radius:0;
   box-shadow:none;
   outline:none;

   &:focus{
      box-shadow:none;
      outline:none;
   }

   @media(max-width:992px){
      padding: 13px 25px;
   }
}
.btn-lg{
   padding: 12px 42px;
   @media(max-width:992px){
      padding:11.6px 40px;
   }
}
.primary-btn {
	background-color:$warning-color !important;
   color:$white;
   border-color: #ffbf00 !important;

   &:hover{
      color:$white;
   }
}

.dark-btn {
	background-color:#484545 !important;
   color:$white;
   border-color: #484545 !important;

   &:hover{
      color:$white;
   }
}


.secondary-btn {
	background-color:$danger-color;
   color:$white;
   &:hover{
      color:$white;
   }
}
.btn-custom-grey{
   background-color:$darkGrey;
   color: $white;
   padding: 5px 15px;
   border: none;
   box-shadow: 0px 0px 10px #00000085;
   &:hover{
      color:$white
   }
}
.secondary-bordered-btn {
	border:1.6px solid $danger-color;
   color:$danger-color;
   font-weight:bold;
   letter-spacing: 1px;
   &:hover{
      color:$danger-color;
   }
}
::placeholder{
   color: #cacbe8;
   font-size: 16px;
}
.font-bold{
   font-family: SegoeUIBold;
}
.font-bold-600{
   font-weight: 600;
}
// reset prepare
.form-group {
	margin-bottom: 27px;
}
.form-control {
	border-radius: 0;
	height: calc(3.3em + 1.05rem + 2px);
	background-color: #151515;
   border: 1px solid #151515;
   color: #cacbe8;
   @media(max-width:992px){
      height: calc(2.3em + 1.05rem + 2px);
   }
   &:focus{
      box-shadow:0 0 0 .2rem rgba(#151515,.25);
      background-color: #151515;
      border: 1px solid #151515;
   }
}
.input-group-text {
	padding:9px 28px;
	color: #fff;
	background-color:#151515;
	border: 1px solid #151515;
   border-radius: 0;
   flex-direction:column;
   height: calc(3.3em + 1.05rem + 2px);
   @media(max-width:992px){
      height: calc(2.3em + 1.05rem + 2px);
      padding:6px 28px;
   }
   @media(max-width:767px){
      padding:6px 8px;
   }
}
.input-icon {
	font-size: 26px;
}
.primary-color{
   background-color: #ffbf00;
}

/*Sarwan*/
.modal-header{
   border-bottom: 0;
}
.modal-body{
   color: #000;
}
.tron-extens a{
   color:#000;
}
.activetd{
   color:#fff !important;
}