.sideNav-icon {
	display: inline-block;
   .menu-icon {
      vertical-align: middle;
      font-size: 27px;
      color: #fff;
      padding: 0px 3px;
   }
   @media(min-width:992px){
      display: none;
   }
}
.navigation-left {
	background-color: #262626;
   box-shadow: 2px 0 3px 0 rgba(0, 0, 0, 0.16);
   transition: 0.5s;
	position: fixed;
	top:121px;
	left: 0;
	right: auto;
   bottom: 0;
   height:calc(100vh - 95px);
   top:95px;
   width: 125px;
   // z-index: 99;
   overflow: hidden;
   ul {
      overflow-y: auto;
      max-height: 100%;
      overflow-x: hidden;
      flex-wrap: wrap;
      flex-direction: row !important;
      margin-right: -19px;
         @media(max-width:992px){
            margin-right: -1px;
         }
      }
   @media(max-width:992px){
      display:none;
   }

   // @media(max-width:1200px){
   //    height: calc(100vh - 87.18px);
   //    top: 87.18px;
   //    width: 140px;
   // }
   .sidenav-close {
      position: absolute;
      right: 10px;
      top: 5px;
      left: auto;
      bottom: auto;
      @media(min-width:992px){
         display: none;
      }
   }
   .nav-link{
      padding:13px 30px;
      text-align: center;
      text-align: center;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.33;
      border-bottom:2px solid #3a3a3a;
      box-shadow:3px 0 3px 0 rgba(0, 0, 0, 0.33);
      letter-spacing: 0.6px;
 

      .icon-wrap {
         margin-bottom: 8px;
      }
      .color-ico{
         display: none;
      }
      &.active{
         border-left:6px solid $warning-color;
         background-color: #393939;
         .color-ico{
            display: block;
         }
         .white-ico{
            display: none;
         }
      }
   }
}