@import "../fonts/segoe.css";
@import "theme/custom-var";
@import "theme/reset";
@import "theme/common";
@import "theme/header";
@import "theme/sidebar";
@import "theme/home";
@import "theme/transfer";
@import "theme/claim";
@import "theme/refer";
@import "theme/transform";
@import "theme/stake";




.react-datepicker-wrapper{
width:100%;

.react-datepicker__input-container input{
    border-radius: 0;
    height: calc(3.3em + 1.05rem + 2px);
    background-color: #151515;
    border: 1px solid #151515;
    color: #cacbe8;
    padding:6px 12px;
    width:100%;

    &:focus{
    border:none;
    }
    @media(max-width:992px){
    height:calc(2.3em + 1.05rem + 2px);
    }
    }
}


.react-datepicker__day--selected{
    background-color:$warning-color !important;
}

.errorToast{
    position:fixed;
    top:110px;
    right:10px;
    z-index:9999999;
}


.pageLoader{
    background:rgba(0, 0, 0, 0.877);
    position: fixed;
    right:0;
    top:0;
    left:0;
    min-height:100vh;
    height: 100%;
    width:100%;
    z-index:9999999;
    overflow:hidden;

    h5{
      font-size: 18px;
      letter-spacing: 0.8px;
    }
}




.sa {
  width: 140px;
  height: 140px;
  padding: 26px;

  &-warning {
    border-radius: 50%;
    border: 4px solid #F8BB86;
    box-sizing: content-box;
    height: 80px;
    padding: 0;
    position: relative;

    width: 80px;
    animation: scaleWarning 0.75s infinite alternate;
    &:after, &:before {
      content: '';
            border-radius: 50%;
            height: 100%;
      position: absolute;
            width: 100%;
    }
    &:before {
            display: inline-block;
            opacity: 0;
            animation: pulseWarning 2s linear infinite;
    }
    &:after {
            display: block;
            z-index: 1;
    }
    &-body {
      background-color: #F8BB86;
      border-radius: 2px;
      height: 47px;
      left: 50%;
      margin-left: -2px;
      position: absolute;
      top: 10px;
      width: 5px;
      z-index: 2;
      animation: pulseWarningIns 0.75s infinite alternate;
    }
    &-dot {
      background-color: #F8BB86;
      border-radius: 50%;
      bottom: 10px;
      height: 7px;
      left: 50%;
      margin-left: -3px;
      position: absolute;
      width: 7px;
      z-index: 2;
      animation: pulseWarningIns 0.75s infinite alternate;
    }
  }
}
@keyframes scaleWarning {
    0% {
        transform: scale(1);
    }
    30% {
        transform: scale(1.02);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes pulseWarning {
    0% {
        transform: scale(1);
        opacity: 0.5;
    }
    30% {
        transform: scale(1);
        opacity: 0.5;
    }
    100% {
        background-color: #F8BB86;
        transform: scale(2);
        opacity: 0;
    }
}
@keyframes pulseWarningIns {
  0% {
    background-color: #F8D486;
  }
  100% {
    background-color: #F8BB86;
  }
}
