.main-content {
   .stats-list {
      padding: 12px 120px 12px 80px;
      border-bottom: 3px solid #151515;
      @media(max-width:992px){
         padding: 12px 40px 12px 40px;
      }
      @media(max-width:575px){
         padding: 12px 15px 12px 15px;
      }
      &:last-child{
         border-bottom: 0;
      }
      .sts-lft {
         flex: 0 0 65%;
      }
      .sts-rt {
         flex: 0 0 35%;
      }
   }
}