.stake-page{
   .input-wrap {
      padding:80px 95px;
      @media(max-width:1440px){
         padding:40px 50px;
      }
      @media(max-width:767px){
         padding: 20px;
      }
   }
   .stakes-content {
      padding: 80px 80px 80px 0;
      @media(max-width:1440px){
         padding: 40px 50px 39px 0px;
      }
      @media(max-width:1200px){
         padding: 40px 50px 39px 40px;
      }
      @media(max-width:767px){
         padding: 20px;
      }
      .stakes-list {
         max-width: 260px;
         margin-bottom: 52px;
         @media(max-width:575px){
            max-width:100%;
         }
         @media(min-width:1200px){
            max-width:300px;
         }
         .stake-title {
            font-family: SegoeUIBold;
         }
         .data-item {
            color:#9798b6;
         }
         .data-title{
            font-family: SegoeUIBold;
            color:#d2d3dc;
         }
         .sub-des{
            color:#d2d3dc;
            font-size: 12px;
            line-height: 2.25;
            color: #78799a;
         }
      }
   }
}


.btnCheck{
   position:fixed;
   top:0;
   z-index:9999999999;
}