.main-header {
      box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.16);
      background-color: #262626;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: auto;
      width: 100%;
      // z-index: 999;
   .navbar {
      padding: .5rem 0;
      @media(max-width:1200px){
         padding: .5rem 0;
      }
      .logo {
            padding: 15px 0;
         .logo-img {
            display: block;
            width: 68px;
            margin: 0 auto;
            @media(max-width:1440px){
               width: 56px;
            }
         }
         display: inline-block;
         width:calc(125px);
         box-shadow: 2px 0 3px 0 rgba(0, 0, 0, 0.16);
 
         @media(max-width:992px){
            box-shadow: none;
            width:95px;
         }
         @media(max-width:350px){
            width: calc(120px);
         }
      }
      .w-menu-item {
         margin-right: 13px;
         img{
            @media(max-width:450px){
               width:30px;
            }
         }
      }
      .right-widget{
         padding-right: 55px;
         @media(max-width:1440px){
            padding-right: 30px;
         }
         @media(max-width:991px){
            padding-right: 15px;
         }
      }
      .dropdown .dropdown-toggle {
         @media(max-width:450px){
            padding-right: 0px;
         }
      }
   }
}
