.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




/* loader style */



.loader {
  height: 2px;
  background: #ffbf00;
  position: absolute;
  width: 100%;
  left:0;
}
.loader span {
  position: absolute;
  display: block;
  height: 4px;
  width: 10px;
  box-shadow: #ffbf00 0 0 12px;
  animation: animate 2s infinite ease-in-out;
  opacity: 0;
}
.loader span:nth-child(1) {
  animation-delay: 0s;
}
.loader span:nth-child(2) {
  animation-delay: 0.5s;
}
.loader span:nth-child(3) {
  animation-delay: 1s;
}
.loader span:nth-child(4) {
  animation-delay: 1.5s;
}
@keyframes animate {
  0% {
    left: 0;
    opacity: 0;
 }
  10% {
    opacity: 0;
    left: 0;
 }
  20% {
    opacity: 1;
 }
  80% {
    opacity: 1;
 }
  90% {
    opacity: 0;
    left: calc(100% - 10px);
 }
  100% {
    opacity: 0;
    left: calc(100% - 10px);
 }
}


.blink-soft {
  animation: blinker 1.7s linear infinite;
}
@keyframes blinker {
  50% {
    opacity: 0.3;
  }
}


