.calcultate-item {
	padding: 13px 25px;
	flex: 0 0 160px;
   max-width: 160px;
   @media(max-width:992px){
      padding: 9px 15px;
      flex: 0 0 120px;
      max-width:120px;
   }
   @media(max-width:450px){
      padding: 9px 10px;
      flex: 0 0 94px;
      max-width:94px;
   }
   span{
      font-size:18px;
      font-weight:600;
      @media(max-width:992px){
         font-size:18px;
      }
      @media(max-width:450px){
         font-size: 16px;
      }
   }
   .ttl-title{
      color: $white;
   }
   .amount{
      color: $warning-color;
   }
}
.transform-page{
   table{
      td{
         height: 51px;
      }
   }
   .calculate-wrap {
      box-shadow: 0 0 4.5px 0 rgba(0, 0, 0, 0.31);
      background-color: rgb(27, 27, 27);
   }
}

.lastTransform td{
   color:white;
}

.inputDisabled{
   background-color: rgb(53, 53, 53) !important;
   border:none !important;
}