.main-content {
   transition: width .24s ease-in-out;
   position: relative;
   margin-top:95px;
   min-height: calc(100vh - 95px);
   width: 100%;
   float: right;
   padding: 0 40px 0;
   padding-top: 49px;
   z-index:-1;

   @media(max-width:992px){
      padding: 0 40px 0;
      padding-top: 40px;
   }
   @media(max-width:767px){
      padding: 0 20px 0;
      padding-top: 20px;
   }
   &.sidebar-open {
      @media(min-width:993px){
         width: calc(100% - 125px);
      }
      @media(max-width:1200px){
         width:calc(100% - 140px);
      }
      @media(max-width:992px){
         width: 100%;
      }
   }
   .alert-box {
      background: #d13d3d;
      padding: 7.5px 27px;
      border-radius: 3px;
      margin-bottom: 44px;
      .alert-icon {
         margin-right: 15px;
         font-size: 21px;
      }
      @media(max-width:991px){
         padding:7.5px 15px;
      }
   }
}
.card {
   border-radius: 4px;
   box-shadow: 0 0 4.5px 0 rgba($shadow, 0.3);
   background-color:$dark-color;
 }
// dropdown
.dropdown{
	.dropdown-toggle{
         display: block;
         padding: 10px;
         padding-right: 25px;
         border-radius: 3px;
         border: solid 1px $primary-color;
         background-color: $primary-color;
         font-size: 18px;
         color: #cacbe8;
         &:focus{
            box-shadow: none;
            border: solid 1px $primary-color;
            background-color: $primary-color;
         }
		&::after {
         vertical-align: middle;
		}
	}
	.dropdown-menu{
		border: solid 1px #212529;
      background-color: #212529;
      width: 100%;
      color: #cacbe8;
      min-width:8rem;
		.dropdown-item{
			color: #7f8b98;
			background-color: transparent !important;
			font-size: 14px;
			padding: 5px 10px;
			&.active,&:hover{
				color: #ffffff;
			}
		}
	}
}
// table
.table{
   thead{
      th{
         border-top:0;
         border-bottom: 2px solid #151515;
         // color: #cacbe8;
         color:#cacbe8;
         text-align: center;
         white-space: nowrap;
         padding: 13px 48px;
         &:first-child{
            text-align: left;
         }
         &:last-child{
            text-align:right;
         }
      }
   }
   td{
    border-top:2px solid #151515;
    color: #cacbe8;
    text-align: center;
    white-space: nowrap;
    padding: 13px 48px;
      &:first-child{
       text-align: left;
       }
      &:last-child{
         text-align:right;
      }
   }
   .dropdown{
      .dropdown-toggle{
         border: solid 1px $dark-color;
         background-color: $dark-color;
         color: #cacbe8;
         padding: 0;
      }
   }
}
.no-data {
	margin-bottom: 23px;
}
// Empty Table
.empty-table {
   position: relative;
   &::before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      height: 100%;
      width: 100%;
      opacity: 0.75;
      background-color: #262626;
   }
      .switch-wrap{
         opacity: 0.75;
      }
      
   .table{
      opacity: 0.75;
      td{
         height: 51px;
      }
   }
   .data-box {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
      text-align: center;
      width:100%;
   }
}
// custom switch
.custom-switch {
   .custom-control-input {
		&:checked {
			~ {
				.custom-control-label {
					&::after {
						background-color:$white;
						transform: translateX(1.45rem);
               }
               &::before {
                  color: white;
                  border-color: #ffbf00;
                  background-color:#ffbf00;
               }
				}
			}
		}
	}
   .custom-control-label {
      font-size: 24px;
      line-height: 1.33;
      &::before {
         width:46.6px;
         height:20.6px;
         border-radius: 0.8rem;
         background-color:#151515;
         border: #151515 1px;
         left: -3.85rem;
         top: 0.55rem;
      }
      &::after {
			top:calc(.25rem + 7.1px);
			left: calc(-3.85rem + 4px);
			width:16px;
			height:16px;
			background-color:$white;
      }
      @media(max-width:767px){
         font-size:16px;
      }
   }
}
// prep-main
.prep-main {
	background-color: #4d4f6f;
	padding: 5px 12px;
   border-radius: 2px;
   font-size: 14px;
   @media(max-width:992px){
      padding: 0px 12px;
   }
   i{
      @media(max-width:575px){
         font-size:18px;
      }
   }
}


// hiding number arrow in input type number

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

