/* Scrollbar Styling chrome */
::-webkit-scrollbar {
   width: 8px;
   height:8px;
}

::-webkit-scrollbar-track {
   background-color: #ebebeb;
   -webkit-border-radius: 3px;
   border-radius:3px;
}

::-webkit-scrollbar-thumb {
   -webkit-border-radius: 3px;
   border-radius: 3px;
   background: #6d6d6d; 
}


// button disabled
button:disabled,
button[disabled]{
opacity:0.3;
cursor:no-drop;
}